import React, { FC, useState } from 'react';
import { ExternalLinks, InternalLinks } from '../../../utils/links';
import {
  StyledFAQ,
  QuestionsList,
  Question,
  FaqItem,
  Answer,
  ExpandImage,
  QuestionContent,
} from './styled';
import { Text } from '../Text';
import CustomLink from '../Link';
import { colors } from '../../../utils/colors';
import AnimateHeight from 'react-animate-height';
import expandImage from '../../../images/Icons/expandArrow.png';

type FAQItem = {
  question: string;
  answer: string;
};

interface Props {
  //   title: string;
  //   description: string;
}

const faqItems: FAQItem[] = [
  {
    question: `What is Telgorithms minimum monthly messaging volume?`,
    answer: `At this time we require new customers to commit to a minimum of 500K messages per month.`,
  },
  {
    question: `Why do I have to be a Campaign Service Provider (CSP) to run this trial?`,
    answer: `All of Telgorithms customers are registered with The Campaign Registry (TCR) as CSPs. As a CSP, you are the primary user of TCR and therefore maintain full control of your Brands and Campaigns over time. We believe this is the proper and intended path for software providers to take for long-term success and autonomy over their 10DLC messaging.`,
  },
  {
    question: `How long does it take to integrate with Telgorithms SMS APIs?`,
    answer: `If you are already a CSP with TCR, you can integrate with Telgorithm with minimal coding. This is often completed in less than 24 hours.`,
  },
  {
    question: 'How long is the PAYG trial?',
    answer: `The trial starts at 30 days. We encourage you to share or create a minimum of 2-5 Campaigns with us so you can best experience our product and services. There is no maximum, youre welcome to test as many Campaigns as youd like.`,
  },
  {
    question: 'What happens if I decide to become a Telgorithm customer after my trial?',
    answer: `Great news! Because youve already completed integration, getting fully onboarded with Telgorithm becomes even faster and easier. Well ensure you have a smooth migration process without any service disruptions.`,
  },
];

const FAQ: FC<Props> = () => {
  const [expandedQuestions, setExpandedQuestions] = useState<number[]>([]);

  const toggleQuestionExpand = (questionIndex: number) => {
    if (expandedQuestions.includes(questionIndex)) {
      setExpandedQuestions(
        expandedQuestions.filter(
          (expandedQuestion) => expandedQuestion !== questionIndex,
        ),
      );
    } else {
      setExpandedQuestions([...expandedQuestions, questionIndex]);
    }
  };

  return (
    <StyledFAQ>
      <Text.Title.Bold.lg>FAQs</Text.Title.Bold.lg>
      <QuestionsList>
        {faqItems.map((faqItem, index) => (
          <FaqItem onClick={() => toggleQuestionExpand(index)} key={faqItem.question}>
            <QuestionContent>
              <Question>
                <Text.Title.Bold.md>{faqItem.question}</Text.Title.Bold.md>
              </Question>
              <AnimateHeight
                duration={500}
                height={expandedQuestions.includes(index) ? 'auto' : 0}
              >
                <Answer>
                  <Text.Regular.Normal.md>{faqItem.answer}</Text.Regular.Normal.md>
                </Answer>
              </AnimateHeight>
            </QuestionContent>
            <ExpandImage
              expanded={expandedQuestions.includes(index)}
            >
              <img src={expandImage}></img>
            </ExpandImage>
          </FaqItem>
        ))}
      </QuestionsList>
    </StyledFAQ>
  );
};

export default FAQ;
